<template>
    <div class="classes" :key="rerenderer">
        <div class="page-headline">
             השיעורים שלי
        </div>
        <div class="headline"> שיעורים שנקבעו </div>
        <div class="sched">
            <div v-if="!scheduledLessons.length">לא נקבעו שיעורים</div>
            <div v-for="lesson in scheduledLessons" :key="lesson.id">
                <lesson-request v-if="renderComponent"
                    :lesson="lesson"
                    :isRequest="false"
                    @re-render="updateLessons"
                />
            </div>
        </div>
        <div class="headline"> בקשות שיעור </div>
        <div class="sched">
            <div v-if="!lessonRequests.length">אין בקשות</div>
            <div v-else v-for="request in lessonRequests" :key="request.id">
                <lesson-request v-if="renderComponent"
                    :lesson="request"
                    :isRequest='true'
                    @re-render="updateLessons"
                />
            </div>
        </div>
        <div class="headline"> חלונות פנויים </div>
        <div class="sched">
            <div v-if="!freeTimes.length">אין חלונות זמן פנויים</div>
            <div v-else v-for="freeTime in freeTimes" :key="freeTime.id">
                <lesson-request v-if="renderComponent"
                    :lesson="freeTime"
                    :isRequest="false"
                    @re-render="updateLessons"
                />
            </div>
        </div>
        <div class="new-avialiable-class" @click="openNewTimeSlot">
            <div> הכנס חלון זמן חדש </div>
        </div>
        <div class="new-time-slot" v-if="openTimeSlotsAddition">
            <div class="avialiable_time">
                <div>
                    <date-picker v-if="renderComponent" v-model="chosen_date" aria-placeholder="" ></date-picker>
                </div>
                <div class="time-pickers">
                    <div class="time">
                        <vue-timepicker v-if="renderComponent" minute-interval="15" v-model="hour_start" placeholder="הכנס שעת התחלה" manual-input hide-dropdown style="direction: ltr"></vue-timepicker>
                    </div>
                    <div class="time">
                        <vue-timepicker v-if="renderComponent" minute-interval="15" v-model="hour_finish" placeholder="הכנס שעת סיום" manual-input hide-dropdown style="direction: ltr"></vue-timepicker>
                    </div>
                </div>
                <div class="add-button" @click="addNewTimeSlot">
                    הוסף חלון שיעור  
                </div>
                <div v-if="dateError" class="error-message">
                    יש למלא תאריך, שעת התחלה ושעת סיום
                </div>
                <div v-for="time in time_slots" :key="time.start">
                    <div class="time-slot">
                        <span class="slot-date">{{getDate(time.date)}}</span>
                        <span class="slot-time">{{getTime(time.startTime, time.endTime)}}</span>
                    </div>
                </div>
            </div>
        </div>
    </div>
    
</template>

<script>
    import { firebase } from "@firebase/app";
    import LessonRequest from '@/components/LessonRequest.vue';
    import DatePicker from 'vue2-datepicker';
    import 'vue2-datepicker/index.css';
    import VueTimepicker from 'vue2-timepicker'
    import 'vue2-timepicker/dist/VueTimepicker.css'

    export default {
        components: {
            LessonRequest,
            DatePicker,
            VueTimepicker
        },
        data() {
            return {
                rerenderer: 0,
                lessonRequests: [],
                scheduledLessons: [],
                freeTimes:[],
                openTimeSlotsAddition: false,
                hour_start: null,
                hour_finish: null,
                dateError: false,
                time_slots:[],
                chosen_date: null,
                renderComponent: true,
            }
        },
         created(){
            this.updateLessons();
         },
        methods: {
            updateLessons() {
                this.lessonRequests = [];
                this.scheduledLessons = [];
                this.freeTimes =[];
                let self = this;
                firebase.firestore().collection('Teachers').doc(this.$store.state.userId).collection('Classes')
                .get()
                .then(dataSnap => {
                    dataSnap.forEach(single_class => {
                        let stat = single_class.data().status;
                        if (stat === "confirmed")
                            self.scheduledLessons.push(single_class)
                        else if (stat === "pending")
                            self.lessonRequests.push(single_class)
                        else 
                            self.freeTimes.push(single_class)
                        console.log(stat)

                })}).catch(error => {
                    console.log(error);
                })   
            },
            openNewTimeSlot() {
               this.openTimeSlotsAddition = !this.openTimeSlotsAddition; 
            },
            testLegalTIime(hour_start, hour_finish){
                //check if hour start >hour finish or if the class is less than 45 or if the class end before it begin
                
                if(hour_start["HH"]>hour_finish["HH"] 
                || (hour_start["HH"]==hour_finish["HH"] &&
                 (hour_start["mm"]> hour_finish["mm"] || hour_finish["mm"]-hour_start["mm"]<45))){
                    return false
                } else
                     return true
            },     
            addNewTimeSlot(){ 
                this.dateError=false
                if (!this.chosen_date || !this.hour_start || !this.hour_finish){
                    this.dateError=true
                } 
                else if(this.testLegalTIime(this.hour_start,this.hour_finish)){
                    
                    firebase.firestore().collection('Teachers').doc(this.$store.state.userId).collection('Classes')
                    .add({
                        date:this.chosen_date,
                        startTime:this.hour_start,
                        endTime:this.hour_finish,
                        student:"",
                        course:"",
                        status: "",
                        student_name: ""
                    })
                    this.chose_date = this.hour_start = this.hour_finish = null
                    
                }
                else{
                    alert("אנא בדוק את זמני השיעור (שיעור מינימלי הוא 45 דק)")
                }
                this.updateLessons();
            },
        },
    }
    
</script>

<style lang="scss" scoped>
.classes{
    background: #fffaf2;
    height: 100vh;
    margin-bottom: 30px;
}
.page-headline {
    color: #715EF6;
    font-size: 1.3rem;
    font-weight: bold;
    margin: 20px;
}
.headline {
    background-color: #aea3f8;;
    border-radius: 0px;
    color: white;
    padding: 5px;
    width:100vw;
    margin-left:-10px;
}
.sched {
    margin-bottom: 15px;
    height: 200px;
    overflow-y:scroll
}
.new-avialiable-class{
    position: fixed;
    bottom:5%;
    margin: auto 25px;
    width: 300px;
    cursor: pointer;
    background-color: #715EF6;
    padding: 5px;
    border-radius: 30px;;
    color:white;
    &:hover{
        opacity: 0.8;
    }
    &:active{
        transform: scale(0.98);
    }
}
.new-time-slot {
    background: rgb(212, 168, 238);
    padding:5px;
    border-radius: 10px;
    position: fixed;
    bottom:10%;
    // margin:0px 5px 0px 0px ;
    width:93%;
}
.time-pickers {
    margin:5px;
    display: flex; 
    justify-content: space-between;
}
.time {
    text-align: right; 
    // max-width:30%;
}

.add-button {
    background-color: rgb(107, 39, 163);
    color:rgb(255, 255, 255);
    border-radius: 30px;
    width:40%;
    padding:3px;
    margin:auto;
}

</style>