<template>
    <div class="container">
        <div class="name-and-rating" @click="toggleOpenSlots">
            <div>{{teacherName}}</div>
            <div>דירוג: {{calc_rate}} ({{raters}})</div>
        </div>
        <vue-slide-up-down :active="openSlots" :duration="400">
            <div v-for="slot in timeSlots" :key=slot.id>
                <div class="time-and-apply">
                     <div class="time-slot">
                         {{getDate(slot.data().date)}}
                         {{getTime(slot.data().startTime, slot.data().endTime) }}
                    </div>
                    <div class="apply" @click="registerLesson(slot)">
                        <span v-if="slot.data().student !== currentStudentId">הרשמה לשיעור</span>
                        <span v-else class="cancel-registration">ביטול הרשמה</span>
                    </div>
                </div>
            </div>
            <div v-if="timeSlots.length < 1"  class="time-and-apply">
                אין חלונות זמן פנויים
            </div>
        </vue-slide-up-down>
    </div>
</template>

<script>
    import { firebase } from "@firebase/app";
    import VueSlideUpDown from 'vue-slide-up-down';

    export default {
        components: {
            VueSlideUpDown,
        },
        props: {
            name: String,
            teacherId: String,
            teacherName: String,
            teacherPhoneNum: String,
            rating: Number,
            raters: Number,
            timeSlots: Array,
            course: String,
            class_id: String
        },
        data() {
            return {
                openSlots: false,
                currentStudentId: this.$store.state.userId,
                registeredToLesson: false,
            }
        },
        computed: {
            userName() {
                return `${this.$store.state.firstName} ${this.$store.state.lastName}` 
            },
            calc_rate(){
                if(this.raters===0){
                    return "ללא דירוג"
                }else
                return Math.round(this.rating/this.raters);
            }
        },
        methods: {
            toggleOpenSlots() {
                this.openSlots = !this.openSlots
            },
             toggleRequestPending() {
                this.requestPending = !this.requestPending
            },
            getDate(timeStamp) {
                let date = new Date(timeStamp * 1000);
                return  date.getDate() +
                        "/"+(date.getMonth()+1) +
                        "/"+(date.getFullYear()-1969)
            },
            getTime(start, finish) {
                return start["HH"] + ':' + start["mm"] + '-' + finish["HH"] + ':' + finish["mm"]
            },
            registerLesson(slot){
                    let lessonStudentId = slot.data();
                    let self = this
                    console.log("updating lesson1 with  "+ self.teacherId)
                    firebase.firestore()
                    .collection('Teachers')
                    .doc(this.teacherId)
                    .collection('Classes')
                    .doc(slot.id)
                    .update({
                        status: slot.data().student === self.currentStudentId ? '' : 'pending',
                        student: slot.data().student === self.currentStudentId? '' : self.$store.state.userId ,
                        course: slot.data().student === self.currentStudentId? '' : self.course,
                        student_name: slot.data().student === self.currentStudentId? '' : self.userName,
                    },{merge:true}).then(()=>{
                        console.log("first"+slot.data().student+"\nsecond"+self.currentStudentId)
                        if (slot.data().student === self.currentStudentId){
                            debugger;
                            firebase.firestore().collection('Students').doc(self.currentStudentId).collection('Classes').doc(slot.id).delete()
                        }
                        else {
                            console.log(self.currentStudentId)
                            firebase.firestore().collection('Students').doc(self.currentStudentId).collection('Classes').doc(slot.id)
                            .set({
                                teacherId: self.teacherId,
                                teacherName: self.teacherName,
                                teacherPhoneNum: self.teacherPhoneNum,
                                classDate: slot.data().date,
                                startTime: slot.data().startTime,
                                endTime: slot.data().endTime,
                                course: self.course,
                                status: slot.data().student === self.currentStudentId ? '' : 'pending'
                            },{merge:true})
                        }
                    })
                    this.updateSlots();
            },
            updateSlots() {
                this.$emit('update-slots');
            }
        }
    }
    
</script>

<style lang="scss" scoped>


.name-and-rating{
    cursor: pointer;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    width: 330px;;
    // max-width: 100%;
    padding: 10px;
    background-color: #aea3f8;;
    border-radius: 10px;
    color: white;
    font-weight: bold;
}

.time-and-apply{
    color: grey;
    width: 330px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 10px;
    background-color: #e0dcfc;
    border-radius: 10px;
    margin: 2px auto;
}
.container{
    display: flex;
    flex-direction: column;
    align-items: center;;
}
.apply {
    background-color: white;
    border-radius: 10px;
    padding:5px 10px 5px 10px;
    cursor: pointer;
}
.cancel-registration{
    color: red;
    font-weight: bold;
}
.no-available-slots{
    background-color: rgb(255, 129, 129);
}
</style>