<template>
  <div class="page-register">
    <!-- <div >
        <img src="../assets/background.svg" alt="" class="background">
    </div> -->
    <go-back/>
      <div class="background">
        <!-- <img src="../assets/background.svg" alt=""> -->
      </div>
      <div class="logo">
        <img src="../assets/LOGO.svg" alt="">
      </div>
      <div class="input-fields">
          <div class="email">
            <input type="text" v-model="email" placeholder="הכנס אימייל" />
          </div>
          <div class="password">
            <input type="password" v-model="password1" placeholder="הכנס סיסמה" />
          </div>
          <div class="password2">
            <input type="password" v-model="password2" placeholder="הכנס סיסמה בשנית" />
          </div>
          <div class="error-message" v-if="passwordsMismatch">
              הסיסמאות אינן תואמות נסה שנית.
          </div>
      </div>
      <div class="sing-up-button" @click="registerAndNavigateToTeacherProfile">
          הירשם כמורה
      </div>
      <div class="sing-up-button" @click="registerAndNavigateToStudentProfile">
          הירשם כתלמיד
      </div>
</div>
</template>
<script>
import { firebase } from "@firebase/app";
import "firebase/auth"
import GoBack from '../components/GoBack.vue';
// import auth from "../firebase"

export default {
  components: { 
    GoBack 
  },
  data() {
    return {
      email: "",
      password1: "",
      password2: "",
      error: "",
      passwordsMismatch: false
    };
  },
  methods: {
    registerAndNavigateToTeacherProfile(){
      this.register("Teacher");
    },
    registerAndNavigateToStudentProfile(){
      this.register("Student");
    },
    async register(userType) {
      
      this.passwordsMismatch = false
     
    if(this.password1==this.password2){
      firebase.auth().createUserWithEmailAndPassword(this.email,this.password1)
      .then((dataSnap)=>{
      // console.log(dataSnap.user.uid)
      this.$store.commit('setUserId', dataSnap.user.uid)
      if(userType==="Teacher"){
        this.$router.push("/teacher-profile");
      }else{
        this.$router.push("/student-profile");
      }
        }).catch(err=>{
          alert(err)
        })
      }
    else{
        alert("סיסמאות אינן תואמות נסה שנית")
      }
    }

    }
  }

</script>

