<template>
    <div class="container">
        <div class="star_container" >
            <v-easy-dialog v-model="simpleDialog">
                <div class=" flex-col">
                    <div class="rating-instructions">
                        אנא דרג איך היה השיעור עם <span style="font-weight: bold">{{passed_teacher_name}}</span>
                    </div>
                    <star-rating v-model="rating">
                        <div>
                            {{rating}}
                        </div>
                    </star-rating>
                    <div class="rate-button">
                        <button @click="doneRating()">דרג</button>
                    </div>
                </div>
            </v-easy-dialog>
        </div>
        <go-to-profile :navigateTo="'student-profile'"/>
        <div class="background_profile">
            <div class="my_lessons">
                <div class="icon1">
                    <img src="../assets/student-img.svg" alt="">
                </div>
                השיעורים שלי
            </div>
            <div class="lesson-banner">
                <div v-for="myClass in myClasses" :key=myClass.id>
                     <lesson-banner
                        :lessonName="myClass.data().course"
                        :classDate="myClass.data().classDate"
                        :startTime="myClass.data().startTime"
                        :endTime="myClass.data().endTime"
                        :tutorName="myClass.data().teacherName"
                        :tutorPhoneNum="myClass.data().teacherPhoneNum"
                        :confirmation="myClass.data().status"
                     />
                </div>
                <div class="new-lesson-button" @click="navigateToNewLesson">
                    <div class="plus-icon">
                        <img src="../assets/plus.svg" alt="">
                    </div>
                    <div>
                        קבע/י שיעור חדש 
                    </div>
                </div>
            </div>
        </div>
     </div>
     
</template>

<script>
    import LessonBanner from '@/components/LessonBanner.vue';
    import { firebase } from "@firebase/app";
    import GoToProfile from '../components/goToProfile.vue';
    import VEasyDialog from 'v-easy-dialog';
    import StarRating from 'vue-star-rating';
    // import Rating from './components/Rating'

    export default {
        components: {
            LessonBanner,
            GoToProfile,
            VEasyDialog,
            StarRating
        },
        data() {
            return {
                myClasses: [],
                userRef:null,
                simpleDialog:false,
                passed_teacher_name:null,
                passed_teacher_id:null,
                current_class:null,
                rating:null,
                new_rate:null,
                rate:null,
                raters:null
            }
        },
        async created () {
            let self = this
            let currentDate = new Date();
            firebase.firestore().collection('Students').doc(this.$store.state.userId).collection('Classes')
            .get()
            .then((docSnapshot) => {
                docSnapshot.forEach(element => {
                    // console.log(element.data().date )
                    // console.log(currentDate )
                    if(element.data().classDate.toDate() < currentDate ){
                        self.passed_teacher_name=element.data().teacherName;
                        self.passed_teacher_id=element.data().teacherId;
                        self.current_class=element;
                        self.simpleDialog=true;
                        
                    }else{
                        self.myClasses.push(element)
                    }
                    
                });

            }).then(()=>firebase.firestore().collection('Students').doc(self.$store.state.userId).get()
            .then((docSnapshot)=>{
                console.log("Here"+ docSnapshot.data().lastName)
                self.$store.commit('setFirstName',docSnapshot.data().firstName)
                self.$store.commit('setLastName',docSnapshot.data().lastName)
            }))
            
        },
 
        methods: {
            navigateToNewLesson() {
                this.$router.push('/new-lesson')
            },
            doneRating(){
                const self=this;
                if(this.passed_teacher_id!=null){
                    let teacherid=self.passed_teacher_id
                    console.log("not null")
                firebase.firestore().collection('Teachers').doc(teacherid).get()
                .then((querySnapShot)=>{
                    self.raters=querySnapShot.data().raters;
                    self.rate=querySnapShot.data().rate +self.rating;
                    // self.new_rate=(self.rate*self.raters+self.rating)/(self.raters+1)
                    self.raters=self.raters+1;
                    // console.log('new rate is '+ self.passed_teacher_id)
                    }).then(()=>
                   firebase.firestore().collection('Teachers').doc(teacherid).
                   update(
                    {      
                        rate:self.rate,
                        raters: self.raters   
                    }
                     )).then(()=>
                     firebase.firestore().collection('Students').doc(self.$store.state.userId).collection('Classes').doc(self.current_class.id).delete()
                     .then(()=>
                     {
                        this.current_class=null;
                        this.passed_teacher_name=null;
                        this.passed_teacher_id=null
                        this.simpleDialog=false
                     }))      
                     
                }
                else{
                    firebase.firestore().collection('Students').doc(this.$store.state.userId).collection('Classes').doc(this.current_class.id).delete()
                        .then(()=>
                        {
                            this.current_class=null;
                            this.passed_teacher_name=null;
                            this.passed_teacher_id=null
                            this.simpleDialog=false
                        })
                }
                

            }            
        },
    }
</script>

<style scoped>
    .container{
        /* display: inline-block;
        align-content: center; */
        display: flex;
        flex-direction: column;
        align-items: center;
        background: #fffaf2;
        height: 100vh;
    }
    .flex-col {
       margin-left: 40px;
    }
    .rating-instructions {
        margin-bottom: 15px;
        margin-left:8px;
        font-family: Arial, Helvetica, sans-serif;
    }
    .rate-button > button{
        margin-top: 20px;
        margin-left: 110px;
        font-family: Arial, Helvetica, sans-serif;
        font-size: 1.2rem;
        background: #715EF6;;
        color: white;
        border: 0px;
        padding: 5px 15px;
        border-radius: 10px;
       
    }
    .rate-button > button.active {
        background: #776bc7;
    }
    .my_lessons{
        cursor: pointer;
        /* white-space: nowrap; */
        color: #715EF6;
        margin: 120px 0px 60px 0px;
        display: flex;
        flex-direction: row;
        justify-content: space-evenly;
        align-items: center;
        padding: 0px;

        width: 328px;
        height: 36px;
        left: 16px;
        top: 110px;

        background: #D4CEFF;
        border-radius: 8px;
        font-weight: bold;
        
    }

    .icon1{
    height: 30px;
    width: 30px;

    }

    .lesson-banner{
    /* margin: 170px 0px 40px 0px; */
    }
    .new-lesson-button{
        position: fixed;
        bottom: 5%;
        left:23%;
        cursor: pointer;
        /* position: flex; */
        /* bottom:10px; */

        display: flex;
        flex-direction: row;
        justify-content: space-around;
        align-items: center;
        width:50%;
        padding:12px;
        border-radius: 30px;
        background-color: #715EF6;

        font-size:1.2rem;
        color:white;


    }
    .plus-icon{
        height: 17px;
        width: 25px;
    }   

</style>