<template>
    <div class="container">
        <go-back/>
        <div class="background_profile">
            <div class="new-lesson">
                שיעור חדש
            </div>
            <div class="course-selection-bar">
                <div class="field-select">
                    <select v-model="selectedFaculty" @change="updateDepartements" >
                        <option :value="null" disabled>בחר/י פקולטה</option>  
                        <option v-for="faculty in faculties" :key="faculty">
                            {{ faculty }}
                        </option>
                    </select>
                </div>
                <div class="field-select">
                    <select v-model="selectedDepartment" @change="updateCourses">
                        <option :value="null" :disabled="!selectedFaculty">באיזו מחלקה?</option>
                        <option v-for="department in departments" :key="department">
                            {{ department }}
                        </option>
                    </select>
                    <div class="field-select">
                        <select v-model="selectedCourse">
                            <option :value="null" disabled>שם קורס</option>
                            <option v-for="course in courses" :key="course">
                                {{ course }}
                            </option>
                        </select>
                    </div>
                </div>
            </div>
            <!-- <span class="course-selection-bar3">
            </span> -->
        
            <span class="search-button" @click="searchLessons">
                <span class="search-icon">
                    <img src="../assets/search-img.svg" alt="">
                </span>
                חפש שיעור
            </span>
            <div v-if="error" class="error-message">
                יש למלא את כל השדות
            </div>
            <div class="teachers-container">
                <div v-for="teacher in teachers" :key="teacher.data().name">
                    <teacher-time-slots 
                        :name="teacher.data().userName"
                        :timeSlots="lessons_map.get(teacher.id)"
                        :teacherId="teacher.id"
                        :teacherPhoneNum="teacher.data().phoneNum"
                        :teacherName="teacher.data().firstName + ' ' + teacher.data().lastName"
                        :course="selectedCourse"
                        :rating="teacher.data().rate"
                        :raters="teacher.data().raters"
                        @update-slots="searchLessons"
                    />
                </div>
                <div v-if="noAvailableLessons">
                    אין מורים זמינים עבור שיעור זה
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import { firebase } from "@firebase/app";
import TeacherTimeSlots from '../components/TeacherTimeSlots.vue';
import GoBack from '@/components/GoBack.vue';

    export default {
        components: {
            TeacherTimeSlots,
            GoBack,
        },
        data() {
            return {
                currentStudentId: this.$store.state.userId,
                selectedFaculty: null,
                selectedDepartment: null,
                selectedCourse: null,
                teachers: [],
                error: false,
                faculties: [],
                lessons_map:new Map,
                lessons:[],
                departments:[],
                courses:[],
                noAvailableLessons: false,
            }
        },
        created () {
            firebase.firestore().collection('פקולטה')
            .get()
            .then(querySnapshot => querySnapshot.forEach(doc => {
                this.faculties.push(doc.id)
            }))
        },
        methods: {
            updateCourses(){
                console.log(this.selectedFaculty);
                this.courses = []
                if (this.selectedDepartment){
                    firebase.firestore().collection('פקולטה').doc(this.selectedFaculty).collection('מחלקה')
                    .doc(this.selectedDepartment).collection('קורס')
                    .get()
                    .then(querySnapshot=> querySnapshot.forEach(doc => {
                        this.courses.push(doc.data().name)
                    }))
                 }
            },
            updateDepartements(){
                this.departments = []
                if (this.selectedFaculty){
                    console.log("in if")
                    firebase.firestore().collection('פקולטה').doc(this.selectedFaculty).collection('מחלקה')
                    .get()
                    .then(querySnapshot => querySnapshot.forEach(doc => {
                        this.departments.push(doc.id)
                     }))
                }

            },
            // currentTeacherSlots(teacherId){
            //     console.log("student is..........." + this.currentStudentId)
            //     let self = this;
            //     let slots = []
            //     firebase.firestore().collection('Teachers').doc(teacherId).collection('Classes')
            //     .get()
            //     .then(querySnapshot => querySnapshot.forEach(slot => {if(slot.data().student === '' || slot.data().student === self.currentStudentId) slots.push(slot)}))
            //     return slots
            // },
            searchLessons() {
                let self=this
                this.noAvailableLessons = false;
                this.error = false;
                this.teachers = []
                if (!this.selectedFaculty || !this.selectedDepartment || !this.selectedCourse)
                    this.error = true;
                else {
                     firebase.firestore()
                     .collection('Teachers')
                     .where('teaching','array-contains',this.selectedCourse)
                     .get()
                     .then(querySnapshot => 
                        querySnapshot.forEach(doc => {
                            console.log("here")
                            firebase.firestore().collection('Teachers').doc(doc.id).collection('Classes').get()
                            .then(querySnapshot=>querySnapshot.forEach(single_class => {
                                // this.lessons.push(d1)
                                console.log("pushed"+single_class)
                                if(single_class.data().student == '' || single_class.data().student === self.currentStudentId) 
                                    self.lessons.push(single_class)
                            
                            })).then(() => {
                                self.lessons_map.set(doc.id,self.lessons)
                                // console.log(self.lessons_map.get(doc.id))
                                console.log("teacher_classes: "+self.lessons.length)
                                self.lessons=[];
                                self.teachers.push(doc); 

                            })
                            .then(()=>{
                                if (self.teachers.length < 1) self.noAvailableLessons = true
                            })     
                        }
                        ))
                }
                
            },
        }
    }
</script>

<style lang="scss" scoped>
    .background_profile{
        width: 70%;
    }
    .container{
        background: #fffaf2;
        // height: 100vh;
        margin-top: 5px;
        direction: rtl;
        display: flex;
        flex-direction: column;
        align-items: center;
    }
    .new-lesson{
        // cursor: pointer;
        background: #D4CEFF;
        color: #715EF6;
        margin: 110px -40px 30px auto;
        display: flex;
        flex-direction: row;
        justify-content: center;
        align-items: center;
        padding: 0px;

        width: 330px;
        height: 36px;
        left: 16px;
        top: 110px;
        border-radius: 8px;
        font-weight: bold;
    }

    .course-selection-bar {
    //  margin:20px;
    }

    .field-select {
        margin-top: 10px;
    }

    .search-button {
        cursor: pointer;
        background-color: #715EF6;;
        color: white;
        width: 200px;
        display: flex;
        flex-direction: row;
        justify-content: center;
        align-items: center;
        width: 200px;
        margin: auto;
        margin: 25px auto;
        padding: 12px;
        border-radius: 30px;
        font-size: 1.2rem;   
    }
  
   .search-icon {
        height: 17px;
        width: 25px;
        margin: 5px;
    }

    .error-message {
        color: red;
        font-weight: bold;
    }
    .teachers-container{
        margin: auto;
        width:100%;
        // margin-left:10px;
    }

    select {
              -webkit-appearance: none;
      -moz-appearance: none;
      appearance: none;
      border: 1px solid rgba(0,0,0,0.38);
      width:100%; 
      padding: 10px;
      /* border: 1px solid rgb(209, 209, 209); */
      background-color: white;
      border-radius: 8px;
      font-size:1.2rem;
    }

</style>