<template>
    <span >
        <span  @click="goToProfile">    
            <img src="../assets/profile-img.svg" alt="<" class="go-to-profile">
        </span>
    </span>
</template>

<script>
    export default {
        props: {
            navigateTo: String
        },
        methods: {
            goToProfile() {
                return this.$router.push(this.navigateTo);
            }
        },
    }
</script>

<style lang="scss" scoped>


.go-to-profile{
    position: absolute;
    top: 3%;
    left: 5%;

    width: 50px;
    height: 50px;
    border-radius: 100px;

    border:0;
    cursor: pointer;
    &:active{
        transform: scale(0.95);
    }

    z-index: 100000000;
}

</style>