/* eslint-disable prettier/prettier */
<template>
    <div class="container">
        <div class="background_profile">
            <div class="background_profile2">
                <div v-if="!step_one_completed" class="personal_details">
                    <go-back/>
                    <div class="profile-img" >
                            <img v-if="!picture" src="../../assets/profile-img.svg" alt="" />
                            <img v-else :src="picture" alt="" />
                    </div>
                    <input type="file" @change="uploadImage" src="../../assets/profile-img.svg">
                    <div class="first-name">
                        <input v-model="firstName" placeholder="שם פרטי" />
                    </div>
                    <div class="last-name">
                        <input v-model="lastName" placeholder="שם משפחה" />
                    </div>
                    <div class="phone-number">
                        <input v-model="phoneNum" placeholder="מספר טלפון" />
                    </div>
                    <div class="about">
                        <textarea v-model="about" placeholder="כמה מילים על עצמך..."/>
                    </div>
                </div>
                
                <div v-else>
                    <img src="../../assets/go-back-arrow.svg" alt="<" @click="toggleSteps" class="back-arrow">
                    <div class="course-selection-bar">
                        <div class="what-do-you-teach">אז מה אתם מלמדים?</div>
                        <div class="field-select faculty">
                            <select v-model="selectedFaculty" @change="updateDepartements" >
                                <option :value="null" disabled>בחר/י פקולטה</option>  
                                <option v-for="faculty in faculties" :key="faculty">
                                    {{ faculty }}
                                </option>
                            </select>
                        </div>
                        <div class="field-select">
                            <select v-model="selectedDepartment" @change="updateCourses">
                                <option :value="null" :disabled="!selectedFaculty">באיזו מחלקה?</option>
                                <option v-for="department in departments" :key="department">
                                    {{ department }}
                                </option>
                            </select>
                        </div>
                        <div class="field-select">
                            <select v-model="selectedCourse">
                                <option :value="null" disabled>שם קורס</option>
                                <option v-for="course in courses" :key="course">
                                    {{ course }}
                                </option>
                            </select>
                        </div>
                        <div class="nav-button" @click="addCourse">הוסף קורס</div>
                        <div v-for="course in coursesTought" :key="course">
                            <div class="single-course">
                                <div>
                                    {{ course }}
                                </div>
                                <div @click="removeCourse(course)"> 
                                    x 
                                </div>
                            </div>
                            
                        </div>
                        <!-- <datepicker v-model="picked" /> -->
                        <div class="avialiable_time">
                            <div>
                                <date-picker v-model="chosen_date" ></date-picker>
                            </div>
                            <div class="times-picking">
                                <div class="start_time">
                                    <vue-timepicker minute-interval="15" v-model="hour_start" placeholder="הכנס שעת התחלה" style="direction: ltr" manual-input hide-dropdown></vue-timepicker>
                                </div>
                                <div class="end_time">
                                    <vue-timepicker minute-interval="15" v-model="hour_finish" placeholder="הכנס שעת סיום" style="direction: ltr" manual-input hide-dropdown></vue-timepicker>
                                </div>
                            </div>
                            <div class="nav-button" @click="add_time_slot">
                                הוסף זמן  
                            </div>
                            <div v-if="dateError" class="error-message">
                                יש למלא תאריך, שעת התחלה ושעת סיום
                            </div>
                            <div v-for="time in time_slots" :key="time.start" class="time-slot">
                                <span>{{getDate(time.date)}}</span>
                                <span>{{getTime(time.startTime, time.endTime)}}</span>
                            </div>
                        </div>
                    </div>
                </div>
                <div @click="toggleSteps" class="nav-button" v-if="!step_one_completed">
                    <span >המשך</span>
                </div>
                <div v-if="step_one_completed" class="save-button" @click="saveChanges">
                    שמור שינויים
                </div>
            </div>
        </div>
    </div>
</template>





<script >
    import { firebase } from "@firebase/app";
    import "firebase/firestore"
    import"firebase/storage";
    import GoBack from '@/components/GoBack.vue';
    import DatePicker from 'vue2-datepicker';
    import 'vue2-datepicker/index.css';
    import VueTimepicker from 'vue2-timepicker'

// CSS
    import 'vue2-timepicker/dist/VueTimepicker.css'
    export default {
        components: {
            DatePicker,
            VueTimepicker,
            GoBack
        },
        data() {
            return {
                firstName: null,
                lastName: null,
                phoneNum: null,
                degree: null,
                imageData: null,
                picture: null,
                selectedFaculty: null,
                selectedDepartment: null,
                selectedCourse: null,
                faculties: [],
                step_one_completed: false,
                about:'',
                coursesTought: [],
                chosen_date: null,
                picked :null,
                hour_start: null,
                hour_finish: null,
                dateError: false,
                time_slots:[],
                departments:[],
                courses:[]
                
                
            }
        },
        created () {
            let self = this
            firebase.firestore().collection('פקולטה')
            .get()
            .then(querySnapshot => querySnapshot.forEach(doc => {
                this.faculties.push(doc.id)
            }))
        },
        methods: {
            updateCourses(){
                console.log(this.selectedFaculty);
                this.courses = []
                if(this.selectedDepartment){
                    firebase.firestore().collection('פקולטה').doc(this.selectedFaculty).collection('מחלקה')
                    .doc(this.selectedDepartment).collection('קורס')
                    .get()
                    .then(querySnapshot=> querySnapshot.forEach(doc => {
                        this.courses.push(doc.data().name)
                    }))
                 }
            },testLegalTIime(hour_start, hour_finish){
                //check if hour start >hour finish or if the class is less than 45 or if the class end before it begin
                if (hour_start["HH"]>hour_finish["HH"] 
                    || (hour_start["HH"]==hour_finish["HH"] &&
                    (hour_start["mm"]> hour_finish["mm"] || hour_finish["mm"]-hour_start["mm"]<45))){
                        return false
                }
                return true
            },
            updateDepartements(){
                this.departments = []
                if(this.selectedFaculty){
                    console.log("in if")
                    firebase.firestore().collection('פקולטה').doc(this.selectedFaculty).collection('מחלקה')
                    .get()
                    .then(querySnapshot => querySnapshot.forEach(doc => {
                        this.departments.push(doc.id)
                     }))
                }

            },
             getDate(timeStamp) {
                 console.log(timeStamp.getTime())
                let date = timeStamp;
                return  date.getDate()+
                        "/"+(date.getMonth()+1)+
                        "/"+date.getFullYear()
            },
            getTime(start, finish) {
                return start["HH"] + ':' + start["mm"] + '-' + finish["HH"] + ':' + finish["mm"]
            },
            toggleSteps(){
                this.step_one_completed = !this.step_one_completed
            },
            async saveChanges(){
                let self = this
                if(this.firstName && this.phoneNum&&this.lastName){
                    let userId = this.$store.state.userId
                    this.$store.commit('setFirstName',this.firstName)
                    this.$store.commit('setLastName',this.lastName)
                    this.onUpload()  
                    console.log("User id is"+ userId)
                    //somtimes takes time for the promise to become the image Url thats why i sleep for 2000ms 
                    setTimeout(() => {  console.log("sleep!"); }, 2000);
                    firebase.firestore().collection('Teachers').doc(userId).set({    
                        firstName: this.firstName,
                        lastName: this.lastName,
                        phoneNum: this.phoneNum,
                        picture: await this.$store.state.userProfilePic,   
                        about: this.about,
                        teaching: this.coursesTought,
                        rate: 0,
                        raters: 0

                    }).then(() => {
                        console.log("Document successfully written!");
                        self.time_slots.forEach(timeSlot => 
                            firebase.firestore().collection('Teachers').doc(userId).collection('Classes').add({
                                course: timeSlot.course,
                                date: timeSlot.date,
                                endTime: timeSlot.endTime,
                                startTime: timeSlot.startTime,
                                status: timeSlot.status,
                                student: timeSlot.student,
                                student_name: timeSlot.student_name
                            }).then(()=>{
                                 console.log(self.$store.state.userProfilePic)
                                self.$router.push("/my-classes");
                            }).catch((err)=> console.log("error while writing Classes")))
                       
                    }).catch((err)=> console.log('error while writing Teacher profile: ' + err))   
                }else
                alert("אנא הכנס טלפון שם משפחה ושם פרטי")
                
            },
            uploadImage(event){
                this.uploadValue=0;
                this.picture=this.imageData;
                this.imageData=event.target.files[0];
            },
            async onUpload(){
                this.picture=null;
                

                firebase.storage().ref().child(`images/${this.$store.state.userId}/${this.imageData.name}`)
                .put(this.imageData)
                .then(fileData => {

                    return fileData.ref.getDownloadURL()
                }).then ((imageUrl)=>{
                    this.picture=imageUrl
                    
                    console.log(imageUrl)
                    if (imageUrl){
                       this.$store.commit('setUserProfilePic',imageUrl)
                    }
                    else//random image Url 
                     this.$store.commit('setUserProfilePic', "https://www.google.com/url?sa=i&url=https%3A%2F%2Ficon-library.com%2Ficon%2Fdefault-profile-icon-17.html&psig=AOvVaw1qPj9oL41xnFzeE76TgG35&ust=1615127788436000&source=images&cd=vfe&ved=0CAIQjRxqFwoTCICe_4Lym-8CFQAAAAAdAAAAABAN")
                })
            },
            addCourse() {
                if(this.coursesTought.indexOf(this.selectedCourse) < 0 && this.selectedCourse)
                    this.coursesTought.push(this.selectedCourse)
            },
            add_time_slot(){
                     this.dateError=false
                 
                    if (!this.chosen_date || !this.hour_start || !this.hour_finish){
                        this.dateError=true
                    } 
                    else if(this.testLegalTIime(this.hour_start,this.hour_finish)){
                        this.time_slots.push(
                            {
                                date:this.chosen_date,
                                startTime:this.hour_start,
                                endTime:this.hour_finish,
                                student:"",
                                course:"",
                                status: "",
                                student_name: "" 
                            }
                        )
                        // firebase.firestore().collection('Teachers').doc(this.$store.state.userId).collection('Classes')
                        // .add({
                        //     date:this.chosen_date,
                        //     startTime:this.hour_start,
                        //     endTime:this.hour_finish,
                        //     student:"",
                        //     course:"",
                        //     status: "",
                        //     student_name: ""
                        // })
                         this.chose_date = this.hour_start = this.hour_finish = null
                        // this.time_slots.push
                    }
                    else{
                        alert("אנא בדוק את הזמנים שהכנסת שיעור (שיעור מינימלי הוא 45 דק)")
                    }
            },
            removeCourse(course) {
                this.coursesTought.splice(this.coursesTought.indexOf(course), 1)
            },
    }
}
</script>
    
<style lang="scss" scoped> 

.container{
     background: #fffaf2;
    height: 100vh;
    direction: rtl;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.nav-button{
    cursor: pointer;
    margin: 20px auto;
    color:white;
    background-color: #715EF6;
    padding: 5px;
    width: 30%;
    border-radius: 30px; 
}
.background_profile{
    // background-image: url("../../assets/background2.svg");
    display: flex;
    flex-direction: column;
    align-items: center;
    top:0px;
    background-repeat: no-repeat;
    background-position: center;
    position: absolute;
    width: 100%;
    height: 100%;
       

}
.back-arrow {
    position: absolute;
    top: 3%;
    left: 5%;

    width: 30px;
    height: 30px;

    border:0;
    cursor: pointer;
    &:active{
        transform: scale(0.95);
    }

    z-index: 900000000000000;
}
.background_profile2{
    position: absolute;
    top:5%;
    right: 12px;
    left: 0;
    width: 93%;
    height: 90%;

    background: linear-gradient(184.94deg, rgba(255, 255, 255, 0.3828) 8px, rgba(212, 206, 255, 0.5046) 94);
    mix-blend-mode: normal;
    border: 1px solid rgba(255, 255, 255, 0.94);
    box-sizing: border-box;
    backdrop-filter: blur(5px);

    border-radius: 8px;
}
.profile-img{
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 90px auto;
    width:120px; 
}
input, select {
     -webkit-appearance: none;
      -moz-appearance: none;
      appearance: none;
    background: white;
    font-size: 1.2rem;
      -webkit-appearance: none;
      -moz-appearance: none;
      appearance: none;
    margin: 5px auto;
    border-radius: 8px;
    width: 85%;
    border: 1px solid rgba(0,0,0,0.38);
    padding: 10px;
}

input[type="file"] {
    z-index: 10;
    position:absolute;
    top: 200px;
    right: 38%;
    background: transparent;
    color:transparent;
    width: 100px;
    height: 100px;
    border: 0px;
    align-content: center;
}
textarea {
    font-size: 1.2rem;
      -webkit-appearance: none;
      -moz-appearance: none;
      appearance: none;
    margin: 10px auto;
    font-family: Arial, Helvetica, sans-serif;
    width: 85%;
    height: 80px;
    border-radius: 5px;
    border: 1px solid rgba(0,0,0,0.38);
    padding: 10px;
}
.phoneNumber {
   margin: 5px; 
}

.degree{
    margin: 5px; 
}
.single-course, .time-slot {
    margin: 10px;
    padding: 10px 15px;
    display: flex;
    justify-content: space-between;
    background-color: #f58576;
    color: white;
    border-radius: 10px;
}

.add_course_btn {
    margin:10px;
}
.start_time, .end_time {

margin-top: 10px;

}


.slot-date {
    margin:10px;
}
.time-slot {
    display: flex;
}
.error-message {
    margin: 10px;
    font-weight: bold;
    color: red
}
.faculty {
    margin-top:10%;
}
.what-do-you-teach {
    margin-right: 30px;
    text-align: right;
    margin-top:20%;
    font-size: 1.1rem;
    font-weight: bold;
    color: #715ef6;
    letter-spacing: 0.06rem;
}
.save-button {
    margin: 10px;
    padding: 10px 15px;
    background-color: #0dcbc0;
    color: white;
    font-weight: bold;
    border-radius: 30px;
}
.times-picking{
    display: flex;
    justify-content: space-between;
}
</style>