<template>
    <div>
        <div class="banner-container" :class="{drillDown: showDetails}" 
            @click="toggleShowDetails" >
                <div class="class-name"
                     :class="{confirmed: confirmation==='confirmed',pending: confirmation==='pending',declined: confirmation==='declined'}" 
                > 
                    {{lessonName}} 
                </div>
                <div>     
                    <div>
                        {{getDate(classDate)}}
                    </div>
                    <div>
                        {{getTime(startTime, endTime) }}
                    </div>
                </div>
                <div v-if="confirmation !== 'confirmed'" @click="removeLesson" class="close"
                    :class="{confirmed: confirmation==='confirmed',pending: confirmation==='pending',declined: confirmation==='declined'}" >
                     X 
                </div>
        </div>
        
         <vue-slide-up-down :active="showDetails" :duration="400">
            <div class="details" >
                    <div> {{tutorName}} </div>
                    <div> {{tutorPhoneNum}} </div>
            </div>
         </vue-slide-up-down>
    </div>
</template>

<script>
    import VueSlideUpDown from 'vue-slide-up-down';
    export default {
        components: {
            VueSlideUpDown,
        },
        data() {
            return {
                showDetails: false
            }
        },
        props: {
            lessonName: String,
            classDate: Object,
            startTime: Object,
            endTime: Object,
            tutorName: String,
            tutorPhoneNum: String,
            confirmation:String
        },
        methods: {
            toggleShowDetails() {
                this.showDetails = !this.showDetails
            },
            removeLesson() {
                //
                //need to remove from teachers list and user list and rate the teacher
            },
            getDate(timeStamp) {
                let date = new Date(timeStamp * 1000);
                return  date.getDate()+
                        "/"+(date.getMonth()+1) +
                        "/"+(date.getFullYear()-1969)
            },
            getTime(start, finish) {
                return start["HH"] + ':' + start["mm"] + '-' + finish["HH"] + ':' + finish["mm"]
            },
        },
    }
</script>

<style scoped>
  
    .banner-container{
        background-color: white;
        border: 1px solid rgb(219, 219, 219);
        direction: rtl;
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        align-items: center;
        width: 90%;
        padding: 10px 15px 10px 15px;
        border-radius: 10px;
        margin-top: 5px;
        cursor: pointer;
        z-index: 1000;;
        transition: margin;
        transition-duration: 0.5s;
    }
    .drillDown{
        border-bottom-left-radius: 0px;
        border-bottom-right-radius: 0px;
        margin-bottom: 0px;
        transition: border-bottom-left-radius, border-bottom-right-radius;
        transition-duration: 0.5s;

    }
    .confirmed{
        background-color:rgb(74, 228, 151)
    }
    .pending{
        background-color:rgb(255, 193, 111)
    }
    .declined{
        background-color:rgb(255, 132, 110)
    }
    .details{
        direction: rtl;
        display: flex;
        flex-direction: row;
        justify-content: space-evenly;

        background-color:  rgb(245, 245, 245);
        border-bottom-left-radius: 10px;
        border-bottom-right-radius: 10px;
        border: 1px solid rgb(219, 219, 219);
        border-top: transparent;
        
        width: 90%;

        /* margin:0px 5px 0px 5px; */
        padding: 10px 15px 10px 15px;
        /* border-radius: 30px; */
    }
    .class-name {
        padding: 10px;
        border-radius: 10px;
    }
    .close {
        padding: 3px 7px;
        border-radius: 100px;
        position:relative;
    }

</style>