<template>
    <div class="container" :class="{request: isRequest}">
        <div class="confirmation-field" v-if="isRequest">
            <span class="confirm" @click="respondToRequest('confirmed')">
                V
            </span>
    
            <span class="decline" @click="respondToRequest('declined')">
                X
            </span>
        </div>
        <div class="request-details">
            <div class="course-name">
                {{lesson.data().course}}
            </div>
            <div class="date-and-time">
                <div class="date">
                    {{getDate(lesson.data().date)}}
                </div>
                <div class="time">
                    {{getTime(lesson.data().startTime, lesson.data().endTime) }}
                </div>
            </div>
            <div>
                {{lesson.data().student_name}}
            </div>
        </div>
    </div>
</template>

<script>
    import { firebase } from "@firebase/app";

    export default {
        props: {
            lesson: Object,
            isRequest: Boolean,
        },
        data() {
            return {
                student_db_ref:null ,
                student_name:null,
            }
        },
        methods: {
            respondToRequest(answer) {
                let self = this
                let studentClasses = []
                firebase.firestore().collection('Students').doc(this.lesson.data().student).collection('Classes').doc(self.lesson.id)
                .update({
                    status: answer
                })
                .then( ()=> firebase.firestore().collection('Teachers').doc(this.$store.state.userId).collection('Classes').doc(self.lesson.id)
                .update({
                    status: answer,
                })).catch((err)=> console.log(err) + "LessonRequests.vue line 56");
                if (answer === 'declined') {
                    firebase.firestore().collection('Teachers').doc(this.$store.state.userId).collection('Classes').doc(self.lesson.id)
                    .update({
                        course: '',
                        student: '',
                        student_name: '',
                        status: ''
                    })
                }
                this.$forceUpdate();
                this.rerender();
                
            },
            getStudentName() {
                
            },
            getDate(timeStamp) {
                let date = new Date(timeStamp * 1000);
                return  date.getDate()+
                        "/"+(date.getMonth()+1)+
                        "/"+(date.getFullYear()-1969)
            },
            getTime(start, finish) {
                return start["HH"] + ':' + start["mm"] + '-' + finish["HH"] + ':' + finish["mm"]
            },
            rerender() {
                this.$emit('re-render');
            }
        },
        // created () {
        //     console.log(this.lesson.studentId)
        //     this.student_db_ref=firebase.firestore().collection('Students').doc(this.lesson.studentId);
        //     this.student_db_ref.get()
        //     .then((docSnapshot) => {
        //         if (docSnapshot.exists) {
        //              debugger;
        //             this.student_name=docSnapshot.data().userName
        //             console.log("recieved userName "+ this.student_name)   
        //         }
        //     })
        // },
    }
</script>

