<template>
    <span >
        <span  @click="goBack">    
            <img src="../assets/go-back-arrow.svg" alt="<" class="go-back">
        </span>
    </span>
</template>

<script>
    export default {
        methods: {
            goBack() {
                return this.$router.go(-1);
            }
        },
    }
</script>

<style lang="scss" scoped>


.go-back{
    position: absolute;
    top: 3%;
    left: 5%;

    width: 30px;
    height: 30px;

    border:0;
    cursor: pointer;
    &:active{
        transform: scale(0.95);
    }

    z-index: 100000000;
}

</style>